function processNextLevel(summary, top, decisionPromptIDs) {
  let results;
  let children = [];
  let childIDs = [];
  let statuses = [];
  let events;
  let newEvent;
  let dummyEvent = {
    id: top.id,
    status: "",
  };

  if (top.subActions.length === 0) {
    events = getEvents(analysis, "id", top.id);
    if (events.length !== 0) {   
      //examine potential duplicates to determine overall status for newEvent
      //need to do more than ... to get a deep copy of an array of objects
      const copyEvents = events.map(a => ({...a}));
      newEvent = examineDups(copyEvents)  
      statuses = [newEvent.status];
      childIDs.push(newEvent.id);
      children.push(newEvent);
    } else {  //didn't find the event; that means it isn't a recognized event or is specified as contraindicated/unnecessary but is not present
      //all contraindicated, unnecessary are specified in problem solutions now.  So their
      //presence will be marked as contraindicated then as an error in the summary
      let status = determineStatusOfNotFound(top.id, decisionPromptIDs)  
      statuses = [status];
      dummyEvent.status = status;
      childIDs.push(top.id);
      children.push(dummyEvent);
    }
    return [children, childIDs, statuses];
  } else {
    let header = {
      id: top.id,
      childIDs: [],
      children: [],
      status: "",
      phaseOrderStatus: "good",
      type: "header",
    };
    top.subActions.forEach((action) => {
      results = processNextLevel(summary, action, decisionPromptIDs);
      header.children = header.children.concat(results[0]);
      header.childIDs = header.childIDs.concat(results[1]);
      statuses = statuses.concat(results[2]);
    });
    header.status = getPhaseStatus(statuses);
    summary[top.id] = [header].concat(header.children);
    delete header.children;
    return [[header], [header.id], [header.status]];
  }
}

let idsNotFound
let itemsByID = undefined

// main entry point (should be the only thing which needs to be exported)
export function summarizeAnalysis(analysisData, scenIEFeedback, itemByIDArray = undefined) {
  idsNotFound = []
  if (!!itemByIDArray){  
    itemsByID = itemByIDArray}
  const {events: finalActions, constraintsViolated, decisionPromptIDs} = analysisData
  let summary = {}; //holds results for condition 2
  let childLookup = {}; //holds parent info for each leaf node
  analysis = finalActions;
  //work through the condition 2 DS and create entries for it and summarize status of children for the current level
  // if (scenarios[scen.name]) {
    let ds = scenIEFeedback.Action
    //scenarios[scen.name].Action;
    processNextLevel(summary, ds, decisionPromptIDs);

    //create a child lookup for summary in order to modify the summary in the
    //appropriate place in next section of code
    for (const [parentKey, entry] of Object.entries(summary)) {
      let children = entry[0].childIDs;
      children.forEach((child) => {
        if (!summary[child]) {
          childLookup[child] = parentKey;
        }
      });
    }

    // store phase level contraint feedback at the header level for condition 2
    // (Note that for condition 1 it was stored during the analysis at the phase
    // header 
    let entryToModify;
    let firstAction;
    let origAction;
    let section;
    let childSection;
    constraintsViolated.forEach((constraint) => {
      firstAction = finalActions.filter((e) =>
          (!!e.labelID && e.labelID === constraint.arg1.id) ||
          e.id === constraint.arg1.id
      );
      if (firstAction.length !== 0) {
        origAction = firstAction[0];
        let secLabel = origAction.labelID;  //labelID is only for phase / subphase
        section = summary[secLabel];
        
        if (!section){
          childSection = childLookup[origAction.id]
          section = summary[childSection]
        }
      }
      else {console.log ("unable to find event in which to report constraint violation: ", constraint)}
      if (!!section) {
        if (constraint.type === "phase") {
          entryToModify = section[0];
          entryToModify.phaseOrderStatus = "misOrdered";
          if (!entryToModify.arg2) {
            entryToModify.arg2 = [constraint.arg2.id];
          } else {
            entryToModify.arg2 = entryToModify.arg2.concat([
              constraint.arg2.id,
            ]);
          }
        } else {  
          entryToModify = section.filter((e) => e.id === origAction.id)[0];
          if (entryToModify.status === "error"){
            entryToModify.status = "misorderedError"
          }
          else {entryToModify.status = "misOrdered"}
          if (!entryToModify.arg2) {
            entryToModify.arg2 = [constraint.arg2.id];
          } else {
            entryToModify.arg2 = entryToModify.arg2.concat([
              constraint.arg2.id,
            ]);
          }
        }
      }
      else {console.log("unable to find find summary section with entry to contain constraint violation report: ", firstAction, constraint)}
    });
  // }
  if (idsNotFound.length !== 0){
    console.log("idsNotFound: ", idsNotFound)
  }
  return summary;
}
