import { useContext } from "react";
import { Switch, Route } from "react-router-dom";

import {
  // PrivateRoute,
  AuthContext,
} from "@meddbriefer/mdb-auth";

import { useFirebase } from "@meddbriefer/mdb-firebase";
import { createCond1Comments, createCond2Debriefing } from "@meddbriefer/scenario-data";

import ObserverLogList from "./ObserverLogList";
import ObserverLog from "./ObserverLog";
import Analysis from "./Analysis";

export default function LogChooser({ redirectToCond1Debriefing, redirectToCond2Debriefing }) {
  const { db } = useFirebase();
  const { userName } = useContext(AuthContext);

  const continueCond1ReviewHandler = (debriefingID) => redirectToCond1Debriefing(debriefingID);
  
  const startCond1ReviewHandler = async (analysis) => {
      console.log("I will create a new review doc and redirect to it", analysis);
      const debriefID = await createCond1Comments(db, analysis, userName);
      redirectToCond1Debriefing(debriefID);
  };

  const continueCond2ReviewHandler = (debriefID) => redirectToCond2Debriefing(debriefID)

  const startCond2ReviewHandler = async (analysis) => {
    console.log("I will create a new review doc and redirect to it", analysis);
    const debriefID = await createCond2Debriefing(db, analysis, userName);
    redirectToCond2Debriefing(debriefID)
  }

  return (
    <Switch>
      <Route path="/logs/:logID/analysis/:analysisID">
        <Analysis
          onCond1StartReview={!redirectToCond1Debriefing ? null : startCond1ReviewHandler}
          onCond1ContinueReview={!redirectToCond1Debriefing ? null : continueCond1ReviewHandler}
          onCond2StartReview={!redirectToCond2Debriefing ? null : startCond2ReviewHandler}
          onCond2ContinueReview={!redirectToCond2Debriefing ? null : continueCond2ReviewHandler}
        />
      </Route>
      <Route path="/logs/:logID" component={ObserverLog} />
      <Route exact path="/" component={ObserverLogList} />
    </Switch>
  );
}
