import { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { useFirebase } from "@meddbriefer/mdb-firebase";
import { getObserverLogList } from "@meddbriefer/scenario-data";

import ObserverLogTable from "../components/ObserverLogTable";

export default function ObserverLogList() {
  const { db } = useFirebase();
  const history = useHistory();
  const [obsLogs, setObsLogs] = useState(null);

  const viewObsLogHandler = (obsLogID) => {
    history.push(`/logs/${obsLogID}`);
  };

  useEffect(() => {
    getObserverLogList(db)
      .then((logs) => setObsLogs(logs))
      .catch((error) => console.error(error));
  }, [db]);

  if (!obsLogs) {
    return <div>Loading Logs</div>;
  }
  return (
    <>
      <h1>Observer Logs</h1>
      <ObserverLogTable logs={obsLogs} viewButtonHandler={viewObsLogHandler} />
    </>
  );
}
