import { useContext, useEffect, useState } from "react";
import { useParams, useHistory } from "react-router-dom";

import { useFirebase } from "@meddbriefer/mdb-firebase";
import { AuthContext } from "@meddbriefer/mdb-auth";
import {
  getLog,
  getPublishedScenarioObjectByID,
  getObserverLogAnalyses,
} from "@meddbriefer/scenario-data";
import {
  analyzeEvents,
  saveAnalysisLog,
} from "@meddbriefer/analysis";

import ObserverLogDetails from "../components/ObserverLogDetails";

export default function ObserverLog() {
  const { db } = useFirebase();
  const history = useHistory();
  const { logID } = useParams();
  const { userName } = useContext(AuthContext);
  const [obsLog, setObsLog] = useState(null);
  const [scenario, setScenario] = useState(null);
  const [analysisLogs, setAnalysisLogs] = useState(null);
  // const [debriefings, setDebriefings] = useState(null)
  // const [doneLoading, setDoneLoading] = useState(false)
  const [updateAnalysisLogs, setUpdateAnalysisLogs] = useState(true);

  const analyzeLogHandler = async () => {
    // console.log("I will analyze now")
    const analysis = analyzeEvents(scenario, obsLog);
    // console.log(userName, analysis)
    await saveAnalysisLog(db, obsLog, analysis, userName);
    setUpdateAnalysisLogs(true);
  };

  const viewAnalysisHandler = (analysisID) => {
    history.push(`/logs/${logID}/analysis/${analysisID}`);
  };

  useEffect(() => {
    const fetchData = async () => {
      const obsLogData = await getLog(db, logID)
      if (obsLogData) {
        setObsLog(obsLogData);
        //console.log("obsLog", obsLogData)
        const scenData = await getPublishedScenarioObjectByID(db, obsLogData.scenarioID)
        if (scenData) {
          setScenario(scenData)
        }
      }
    }
    if (db && logID && !obsLog) {
      fetchData()
    }
  }, [db, logID, obsLog]);

  useEffect(() => {
    if (obsLog && scenario && updateAnalysisLogs) {
      setUpdateAnalysisLogs(false);
      getObserverLogAnalyses(db, logID)
        .then((records) => setAnalysisLogs(records))
        .catch((error) => console.error(error));
    }
  }, [db, logID, obsLog, scenario, updateAnalysisLogs]);

  if (!analysisLogs) {
    return <div>Loading..</div>;
  }
  return (
    <ObserverLogDetails
      log={obsLog}
      scenario={scenario}
      analyses={analysisLogs}
      onAnalyze={analyzeLogHandler}
      onViewAnalysis={viewAnalysisHandler}
    />
  );
}
