import { 
  // useContext, 
  useEffect, 
  useState 
} from "react";
import { 
    useParams, 
    // useHistory 
} from "react-router-dom";

import { useFirebase } from "@meddbriefer/mdb-firebase";
// import { AuthContext } from "@meddbriefer/mdb-auth";
import {
  getAnalysisLog,
  getAnalysisCond1Comments,
  getAnalysisCond2Debriefings
} from "@meddbriefer/scenario-data";

import AnalysisDetails from "../components/AnalysisDetails";

export default function Analysis({
  onCond1StartReview = null,
  onCond1ContinueReview = null,
  onCond2StartReview = null,
  onCond2ContinueReview = null,
}) {
  const { db } = useFirebase();
  const { logID, analysisID } = useParams();
  // const history = useHistory();
  // const { userName } = useContext(AuthContext);
  const [analysis, setAnalysis] = useState(null);
  const [cond1DebLogs, setCond1DebLogs] = useState(null);
  const [cond2DebLogs, setCond2DebLogs] = useState(null);

  useEffect(() => {
    if (db && !analysis) {
      getAnalysisLog(db, analysisID)
        .then((data) => setAnalysis(data))
        .then(() => getAnalysisCond1Comments(db, analysisID))
        .then((comments) => setCond1DebLogs(comments))
        .then(() => getAnalysisCond2Debriefings(db, analysisID))
        .then(debriefings => setCond2DebLogs(debriefings))
        .catch(error => console.error(error))
    }
  }, [db, logID, analysisID, analysis]);

  if (!(cond1DebLogs && cond2DebLogs)) {
    return <div>Loading...</div>;
  }
  return (
    <AnalysisDetails
      analysis={analysis}
      cond1Comments={cond1DebLogs}
      cond2Debriefings={cond2DebLogs}
      onCond1StartReview={onCond1StartReview}
      onCond1ContinueReview={onCond1ContinueReview}
      onCond2StartReview={onCond2StartReview}
      onCond2ContinueReview={onCond2ContinueReview}
    />
  );
}
